, principalmente con intereses económicos y de control representados en diversos mapas. En el siglo XIX, personajes como Francisco José de Caldas elaboran trabajos más allá de enfoques políticos, abordando características de distintos territorios del Virreinato de Nueva Granada; sumado a ello, el enorme aporte de la Expedición Botánica dirigida por José Celestino Mutis, y la visita de Humboldt (1801). Las diferentes respuestas al proceso de independencia colocan al conocimiento geográfico en un lugar de importancia para la construcción de un nuevo país, evidencia de ello son los resultados de la Comisión Corográfica (1850-1859), que busca identificar y conocer los territorios que contribuyan a la construcción de un mapa nacional. Posteriormente, otros personajes, como Francisco Javier Vergara y Velasco, los textos del religioso Justo Ramón, Julio Garavito Armero, Eduardo Acevedo Latorre, José Agustín Blanco, Ernesto Guhl y otro número de científicos y geógrafos, han contribuido al conocimiento espacial de Colombia y su contexto en el ámbito mundial (Díaz, 2010). Observe la siguiente información:")])])]),_c('div',{staticClass:"col-12 col-md-6",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/t3-9.jpg"),"alt":"Texto que describa la imagen"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5 justify-content-center"},[_c('div',{staticClass:"col-12 col-md-4 bgr_24"},[_c('h3',{staticClass:"text-center"},[_vm._v("Geografia")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mb-3"},[_c('img',{attrs:{"src":require("@/assets/curso/tema3/t3-32.png"),"alt":"Texto que describa la imagen"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"p-2"},[_c('b',[_vm._v("En Colombia")]),_vm._v(", la reflexión sobre el espacio se ve reflejada de l